@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&display=swap);
* {
  font-family: 'Roboto Mono', monospace;
}

a:visited {
  color: black;
}

.wrap {
  display: flex;
  max-width: 800px;
  margin: 0 auto;
  padding: 0 32px;
}

@media (max-width: 1199px) {
  .wrap {
    padding: 0 16px;
  }
}

.about {
  margin-bottom: 44px;
  line-height: 25px;
}

.btn {
  color: #2c57fb;
  font-weight: bold;
  padding: 8px 2px;
  text-align: center;
  border: 4px solid #2c57fb;
  text-decoration: none;
  width: 130px;
  height: 30px;
  cursor: pointer;
}

.btn:hover {
  box-shadow: 3px 4px 0px #4e57ef;
}

@media (max-width: 1199px) {
  .btn {
    box-shadow: 3px 4px 0px #4e57ef;
  }
  .btn-my {
    box-shadow: 3px 4px 0px rgb(116, 168, 63);
  }
  .btn-net {
    box-shadow: 3px 4px 0px rgb(80, 82, 78);
  }
}

.btn-my {
  color: rgb(116, 168, 63);
  font-weight: bold;
  padding: 8px 2px;
  text-align: center;
  border: 4px solid rgb(116, 168, 63);
  text-decoration: none;
  width: 130px;
  height: 30px;
  cursor: pointer;
}

.btn-my:hover {
  box-shadow: 3px 4px 0px rgb(116, 168, 63);
}

.btn-net {
  color: rgb(80, 82, 78);
  font-weight: bold;
  padding: 8px 2px;
  text-align: center;
  border: 4px solid rgb(80, 82, 78);
  text-decoration: none;
  width: 130px;
  height: 30px;
  cursor: pointer;
}

.btn-net:hover {
  box-shadow: 3px 4px 0px rgb(80, 82, 78);
}

.btn>a {
  text-decoration: none;
}

.btn-my>a {
  text-decoration: none;
}

.btn-net>a {
  text-decoration: none;
}

.project-list {
  display: flex;
  -webkit-transition: all 0.2 ease-in-out;
  transition: all 0.2 ease-in-out;
}

@media (max-width: 768px) {
  .project-list {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.project-list div {
  margin: 5px;
}

.text {
  color: rgb(116, 168, 63);
}

.text-net {
  color: rgb(80, 82, 78);
}

.text-underline {
  width: 21em;
  padding-bottom: 4px;
  border-bottom: 4px solid black;
}

.text-underline__mod {
  width: 8em;
  padding-bottom: 4px;
  border-bottom: 4px solid black;
}

.text-underline__net {
  width: 5em;
  padding-bottom: 4px;
  border-bottom: 4px solid black;
}

.text-name {
  width: 21em;
  border-bottom: 4px solid black;
}

.text-cv {
  cursor: pointer;
  padding: 10px;
  width: 11em;
  border-bottom: 4px solid red;
}

a {
  cursor: pointer;
  text-decoration: none;
}

.menu {
  margin-top: 10px;
}

@-webkit-keyframes slideInLeft {
  from {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInLeft {
  from {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

.slideInLeft {
  -webkit-animation: slideInLeft 0.7s;
          animation: slideInLeft 0.7s;
}
